.footer{
    background-color: #444444;
    color: #dddddd;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1rem 0.5rem 1rem;
}
.footer span{
    font-family: "motiva italic";
    display: flex;
    align-items: center;
}

.footer span svg{
    font-size: 26px;
    margin: 0 0.5rem;
    color: rgb(230, 40, 40);
}

.footer a{
    color: #dddddd;
    margin: 0 1rem;
}
.footer a svg{
    font-size: 26px;
}


@media only screen and (max-width: 600px){
    .footer{
        padding: 1.5rem;
    }
    .footer span svg{
        font-size: 20px;
        margin: 0 0.3rem;
    }
    .footer a svg{
        font-size: 20px;
    }
    .footer a{
        color: #dddddd;
        margin: 0 0.5rem;
    }
}