.technologies {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  box-sizing: border-box;
  margin-bottom: 2rem;
}

@media only screen and (max-width: 600px) {
  .technologies {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.Technology {
  display: grid;
  grid-template-rows: 5fr 1fr;
}

.Technology > img {
  justify-self: center;
  align-self: center;
  user-select: none;
  object-fit: contain;
}

.Technology > span {
  margin-top: 0.3rem;
  align-self: flex-end;
  justify-self: center;
}
