.NavMenu {
  display: grid;
  grid-template-columns: 6fr 4fr;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}
.NameLogo {
  margin-left: 3.5rem;
}
.actions {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.actions span {
  padding: 10px 20px;
  transition: all 0.3s;
  border-radius: 6px;
}
.actions span:hover {
  background-color: #784cfb;
}
.actions span:hover a {
  color: #ffffff;
}
.actions span a {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  text-decoration: none;
  color: #784cfb;
}


 @media only screen and (max-width: 600px) {
  .NavMenu {
    padding: 1rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .NameLogo {
  margin: 0
  }
  .actions{
    display: none;
  }

} 
