:root {
  --track-width: 5px;
  --primary-color: #784cfb;
}

.Intro {
  height: max-content;
  position: relative;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 2rem;
}
.introSection2 {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.introSection1 {
  position: relative;
  height: min-content;
  display: flex;
  height: 100%;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  padding: 8rem 10rem;
}
.introSection1 p {
  letter-spacing: 1px;
  text-align: justify;
  margin-bottom: 3rem;
  font-weight: 500;
  color: #333333;
}
.introSection1 a {
  margin: 1rem 0;
  align-self: flex-start;
  background-color: var(--primary-color);
  color: #ffffff;
  text-decoration: none;
  padding: 0.6rem 1.2rem;
  border-radius: 4px;
}
.orbits {
  position: relative;
  height: 17rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  margin: 7rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: revolution 10s linear infinite;
}
.orbits::before,
.orbits::after {
  position: absolute;
  content: "";
  aspect-ratio: 1/1;
  border: var(--track-width) solid var(--primary-color);
  border-radius: 50%;
}
.orbits::before {
  height: 13%;
  right: -8%;
  top: -8%;
}
.orbits::after {
  height: 26%;
  bottom: -18%;
  left: -18%;
}
.outer,
.middle,
.inner {
  position: relative;
  border: var(--track-width) solid var(--primary-color);
  border-radius: 50%;
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: none;
  animation: revolution 10s infinite linear;
}
.outer::before,
.middle::before,
.inner::before,
.outer::after,
.middle::after,
.inner::after {
  content: "";
  height: 1rem;
  background-color: #ffffff;
  aspect-ratio: 1/1;
  position: absolute;
}
.outer::before,
.middle::before,
.inner::before {
  top: -0.5rem;
  left: unset;
  right: unset;
}
.outer::after,
.middle::after,
.inner::after {
  bottom: -0.5rem;
  right: unset;
  left: unset;
}
.outer {
  height: 100%;
}
.middle {
  height: 78%;
}
.inner {
  height: 70%;
}

.animation-container {
  margin: unset;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.animation-container img {
  top: unset;
  clip-path: ellipse(47% 50% at 50% 50%);
  height: 20rem;
  position: absolute;
  bottom: 23.6%;
  rotate: -1deg;
  z-index: 1;
}
@media only screen and (max-width: 600px) {
  .Intro {
    position: relative;
    overflow: hidden;
    display: grid;
    grid-template-columns: none;
  }
  .introSection2 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-row: 1;
  }
  .introSection1 {
    position: relative;
    grid-row: 2;
    padding: 1rem;
    height: min-content;
    display: flex;
    flex-direction: column;
  }
  .introSection1 p {
    text-align: justify;
    font-size: 18px;
  }
  .introSection1 a {
    margin: 1rem 0;
    font-size: 18px;
    align-self: flex-end;
    background-color: var(--primary-color);
    color: #ffffff;
    text-decoration: none;
    padding: 0.6rem 2rem;
    border-radius: 4px;
  }
  .orbits {
    position: relative;
    height: 13rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    margin: 7rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: revolution 10s linear infinite;
  }
  .orbits::before,
  .orbits::after {
    position: absolute;
    content: "";
    aspect-ratio: 1/1;
    border: var(--track-width) solid var(--primary-color);
    border-radius: 50%;
  }
  .orbits::before {
    height: 13%;
    right: -8%;
    top: -8%;
  }
  .orbits::after {
    height: 26%;
    bottom: -18%;
    left: -18%;
  }
  .outer,
  .middle,
  .inner {
    position: relative;
    border: var(--track-width) solid var(--primary-color);
    border-radius: 50%;
    aspect-ratio: 1/1;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: none;
    animation: revolution 10s infinite linear;
  }
  .outer::before,
  .middle::before,
  .inner::before,
  .outer::after,
  .middle::after,
  .inner::after {
    content: "";
    height: 1rem;
    background-color: #ffffff;
    aspect-ratio: 1/1;
    position: absolute;
  }
  .outer::before,
  .middle::before,
  .inner::before {
    top: -0.5rem;
    left: unset;
    right: unset;
  }
  .outer::after,
  .middle::after,
  .inner::after {
    bottom: -0.5rem;
    right: unset;
    left: unset;
  }
  .outer {
    height: 100%;
  }
  .middle {
    height: 78%;
  }
  .inner {
    height: 70%;
  }

  .animation-container {
    margin: unset;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .animation-container img {
    top: unset;
    clip-path: ellipse(47.6% 50% at 50% 50%);
    height: 15rem;
    position: absolute;
    bottom: 27%;
    rotate: -1deg;
    z-index: 1;
  }
}
@keyframes revolution {
  from {
    rotate: 360deg;
  }
}
